import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, ModalController, LoadingController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';
import { AuthProvider } from '../../providers/auth/auth';
import { Content, Collections, Exam, ExamTypes, Comment, Note, Marker } from '../../app/app.models';
import { File } from '@ionic-native/file';
import { FileTransfer } from '@ionic-native/file-transfer';
import { take, map, tap } from 'rxjs/operators';
import { AngularFirestore } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { NgProgressComponent } from '@ngx-progressbar/core';

@IonicPage({ segment: "clase/:id/:type" })
@Component({
  selector: "page-clase-detail",
  templateUrl: "clase-detail.html"
})
export class ClaseDetailPage {

  @ViewChild(NgProgressComponent) progressBar: NgProgressComponent;

  @ViewChild("zamnaPlayer")
  public video: any;

  public clase: Content;
  public comentarios: Observable<Comment[]>;
  public resultados: any[] = [];

  public type: string = this.navParams.get("type");
  public id: string = this.navParams.get("id");

  public newComentario: string = "";
  public lc: boolean = false;

  public debug: any;

  public addData: Partial<Note> = {
    title: "",
    text: ""
  };

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private back: BackProvider,
    private toast: ToastController,
    private modal: ModalController,
    private data: AuthProvider,
    private file: File,
    private transfer: FileTransfer,
    private load: LoadingController,
    private afs: AngularFirestore,
    private auth: AuthProvider
  ) {}

  ionViewDidLoad() {
    this.loadClase(this.id);

    this.comentarios = this.afs
      .collection<Comment>(Collections.COMMENT, ref =>
        ref.where("parent_id", "==", this.id)
      )
      .valueChanges();

    /* this.back.getComentarios(this.id, this.type).subscribe(data => this.comentarios = data.reverse())
    this.back.getResultados(this.type, this.back.uid, this.id).subscribe(data => {
      this.resultados = data ? data : []
    }) */
  }

  openModal(id: string, type: string, modal: string) {
    const m = this.modal.create(modal, { id, type });
    m.present();
  }

  openExamen(type, id) {
    if (!this.resultados || this.resultados.length <= 0) {
      const m = this.modal.create("ExamenDetailPage", { type, id });
      m.present();
      m.onDidDismiss(data => {
        if (!data) return;
        this.back
          .getResultados(this.type, this.back.uid, this.id)
          .subscribe(data => (this.resultados = data ? data : []));
      });
    } else {
      const m = this.modal.create("ResultadosExamenPage", {
        type,
        id,
        user: this.back.uid
      });
      m.present();
    }
  }

  deleteResultados(type, id) {
    this.back.deleteResultados(type, this.back.uid, id).subscribe(() => {
      this.ionViewDidLoad();
    });
  }

  async sendComentario(text: string) {
    if (!text) return;
    this.lc = true;
    const comment = {
      id: this.afs.createId(),
      text,
      user: this.auth.user.uid,
      user_name: this.auth.user.displayName,
      user_photo: this.auth.user.photoURL,
      parent_id: this.id,
      date: new Date().toISOString()
    };

    await this.afs
      .collection(Collections.COMMENT)
      .doc(comment.id)
      .set(comment);

    this.newComentario = "";
    this.lc = false;
  }

  goto(marker: Marker) {
    const horas = parseInt(marker.time.hour.toString()) * 3600;
    const minInSecs = parseInt(marker.time.minute.toString()) * 60;
    const t = parseInt(marker.time.second.toString()) + minInSecs + horas;
    this.video.nativeElement.currentTime = t;
  }

  async loadClase(id: string) {
    this.clase = await this.data.getDoc<Content>(Collections.CONTENT, id);
  }

  async downloadVideo() {
    try {
      this.progressBar.start();
      const l = this.load.create({
        content: "Descargando clase, por favor no cierres la aplicación"
      });
      l.present();

      const fileTransfer = this.transfer.create();

      fileTransfer
        .download(
          this.clase.video,
          `${this.file.dataDirectory}/.zamna-cache/${this.clase.id}`
        )
        .then(entry => {
          this.debug = entry;
          localStorage.setItem(
            `offline-${this.clase.id}`,
            JSON.stringify(this.clase)
          );
          this.progressBar.complete();
          l.dismiss();
          this.toast
            .create({
              message:
                "La clase se descargó correctamente, la puedes ver desde la página de descargas.",
              duration: 2000
            })
            .present();
        })
        .catch(error => {
          this.toast.create({
            message: "Ocurrió un error al descargar la clase..."
          });
          this.debug = error;
        });

      fileTransfer.onProgress(e => this.downloadProgress(e));
    } catch (error) {
      this.toast.create({
        message: "Ocurrió un error al descargar la clase..."
      });
      this.debug = error;
    }
  }

  downloadProgress(e: ProgressEvent) {
    console.log(e);
    this.debug = e;
  }

  async listDir() {
    try {
      await this.file.checkDir(this.file.dataDirectory, ".zamna-cache");
      this.debug = { message: ".zamna-cache dir exists" };
      const list = await this.file.listDir(
        this.file.dataDirectory,
        ".zamna-cache"
      );
      this.debug = list;
    } catch (error) {
      this.debug = error;
    }
  }

  async loadPreclase() {
    /*const exam = await this.getExamByContentName(this.content.name)*/
    const exam = await this.afs
      .collection<Exam>(Collections.EXAM, ref =>
        ref
          .where("content.id", "==", this.id)
          .where("type", "==", ExamTypes.PRECLASE)
      )
      .valueChanges()
      .pipe(
        take(1),
        map(exams => (exams ? exams[0] : null))
      )
      .toPromise();

    if (exam) {
      const resuelto = await this.preclaseResuelto(this.auth.user.uid, exam.id);
      console.log("preclase", exam, "resuelto:", resuelto);
      if (!resuelto)
        this.navCtrl.push("ExamenDetailPage", {
          id: exam.id,
          type: "preclase"
        });
    }
  }

  async preclaseResuelto(uid: string, exam: string) {
    return this.afs
      .collection(Collections.EXAM_RESULT, ref =>
        ref.where("exam", "==", exam).where("user", "==", uid)
      )
      .valueChanges()
      .pipe(
        take(1),
        tap(console.log),
        map(results => results && results.length > 0)
      )
      .toPromise();
  }

  async addNota(title: string, text: string) {
    if (!title)
      return this.toast
        .create({ message: "Ingresa un titulo", duration: 2000 })
        .present();
    if (!text)
      return this.toast
        .create({ message: "Ingresa un text", duration: 2000 })
        .present();

    const l = this.load.create({ content: "Agregando..." });
    l.present();

    const id = this.afs.createId();
    await this.afs
      .collection(Collections.NOTE)
      .doc(id)
      .set({
        id,
        title,
        text,
        parent_id: this.id,
        user: this.auth.user.uid
      });

    l.dismiss();

    this.addData = {
      title: "",
      text: ""
    };

    /* this.back.addNota(title, text, this.type, this.id).subscribe(data => {}, err => {
      this.toast.create({message: 'No se pudo agregar tu nota...', duration: 2000}).present()
      l.dismiss()
    }) */
  }
}



